<a name="process57"></a>

<p><br></p>
<p><span class="rvts58">土元素学派（</span><span class="rvts59">Geomancer</span><span class="rvts58">）</span><span class="rvts59">:</span>
<br><img width="128" height="128" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber241.jpg"></p>
<p><span class="rvts21">土元素学派技能一共有</span><span class="rvts20">24</span><span class="rvts21">个。所有的土元素学派技能都基于智力（</span><span class="rvts20">Intelligence</span><span class="rvts21">）属性造成额外的土元素或毒元素伤害加成。</span>
<br><span class="rvts21">土元素学派有很多可以对敌人造成</span><span class="rvts20">AOE</span><span class="rvts21">（范围）伤害的技能。另外，战斗学派中的死灵学派可以很好的配合使用土元素学派技能，触发和引爆效果更佳的酸腐和中毒效果。</span></p>
<p><span class="rvts21">但是土元素技能中的毒元素技能会对不死族敌人造成治疗效果，因此它在使用方面需要有一些考虑。</span>
<br><span class="rvts43"></span>
<br><span class="rvts29">土元素学派技能书可以从以下</span><span class="rvts33">NPC</span><span class="rvts29">中买到（或学到）：</span>
<br><span class="rvts20">1. </span><span class="rvts21">毛尔（</span><span class="rvts20">Maol</span><span class="rvts21">）</span><span class="rvts20"> --牋</span><span class="rvts21">欢乐堡</span>
<br><span class="rvts20">2. </span><span class="rvts21">欧维斯（</span><span class="rvts20">Ovis</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">浮木城</span>
<br><span class="rvts20">3. </span><span class="rvts21">加雷斯（</span><span class="rvts20">Gareth</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">北部废墟，阿玛迪亚避难所</span>
<br><span class="rvts20">4. </span><span class="rvts21">拖瓦（</span><span class="rvts20">Tovah</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">精灵营地，死神海岸</span>
<br><span class="rvts20">5.</span><span class="rvts21">阿尔麦拉（</span><span class="rvts20">Almira</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">复仇女士号</span>
<br><span class="rvts20">6. </span><span class="rvts21">黑环炼金师（</span><span class="rvts20">Black Ring Alchemist</span><span class="rvts21">）</span><span class="rvts20"> --</span><span class="rvts21">费勒斯神庙南部，无名岛</span>
<br><span class="rvts20">7. </span><span class="rvts21">女祭司杜娜（</span><span class="rvts20">Priestess Duna</span><span class="rvts21">）</span><span class="rvts20"> --</span><span class="rvts21">泰森地留斯神庙，无名岛</span>
<br><span class="rvts20">8.</span><span class="rvts21">商人弗利尔（</span><span class="rvts20">Trader Friel</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿克斯</span>
